.class-list {
  padding: 20px;
  color: rgb(40, 38, 38);

  h2 {
    margin-bottom: 20px;
    margin-left: 45%;
    font-weight: bold;
  }

  .rows {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;

    .card {
      width: 250px;
      height: 300px;
      background: #e5e5e5;
      border: none;
      box-shadow: 15px 20px 20px rgba(0, 0, 0, 0.3), inset 4px 4px 10px white;
      border-radius: 20px;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-bottom: 30px;
      cursor: pointer;
      transition: all 0.3s ease-in-out;

      img {
        width: 280px;
        height: 180px;
        border-radius: 20px;
        object-fit: cover;
      }


    }


    .card:hover {
      box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.3), inset -4px -4px 10px white;
    }

    .class-details {
      text-align: center;
      min-height: 200px;
      max-height: fit-content;
      margin-top: 18px;
      font-size: small;
      display: flex;
      flex-direction: column;
      align-items: center;

      .subject-btn {
        background-color: #4d2c5e;

        color: #fff;
        border: none;
        padding: 10px 20px;
        border-radius: 5px;
        font-size: 16px;
        cursor: pointer;
        transition: background-color 0.3s ease;
      }
    }


    .btn:hover {
      background-color: #72219b;
    }

  }

}