@import '../../config/color.scss'; // Import color variables
// Mylearning.scss

.my-learning-container {
  margin: -20px;
  padding: 20px;
 
  .my-learning-heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    color: $primary-color;
    margin-top: 100px;
    font-size: 24px;
    margin-bottom: 20px;
  }

  .course-table {
    width: 80%;
    margin-left: 100px;
    border-collapse: collapse;
  
  }

  .course-table th,
  .course-table td {
    border: 1px solid #ccc;
    padding: 12px;
    text-align: left;
  }

  .course-table th {
    background-color: $primary-color;
    color: $text-color;
  }

  .course-table td {
    background-color: $background-color;
  }

  .course-table tbody tr:hover {
    background-color: #f0f0f0;
  }

  .course-table button {
    padding: 8px 12px;
    background-color: $button-background;
    color: $button-color;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }

  .course-table button:hover {
    background-color: darken($button-background, 10%);
  }
  .footer-home{
    background-color: #24262b;
    position: absolute;
    top: 630px;
    left: 0;
    right: 0;
  }

}