@import '../../config/color.scss'; // Import color variables

.checkout {

  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;

  .checkout-container {
    width: 80%;
    position: absolute;
    top: 100px;
    padding: 20px;
    background-color: $background-color;
    border-radius: 8px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  }

  .checkout-heading {
    color: $primary-color;
    font-size: 24px;
    margin-bottom: 20px;
  }

  .course-list {
    margin-bottom: 20px;
  }

  .course-list-heading {
    color: $primary-color;
    font-size: 20px;
    margin-bottom: 10px;
  }

  .course-list-items {
    list-style: none;
    padding: 0;
  }

  .course-list-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 8px;
  }

  .course-title {
    color: #000;
    /* Adjust course title color */
  }

  .course-price {
    color: $primary-color;
  }

  .payment-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .total-price {
    color: $primary-color;
    font-size: 18px;
  }

  .payment-button {
    color: $button-color;
    background-color: $button-background;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: darken($button-background, 10%);
      /* Darken button color on hover */
    }
  }

  @media (max-width: 430px) {

    .course-price {
      display: none;
    }
  }

}