.cart {
  margin-top: 0px;
  position: relative;

  .cart-container {
    width: 90%;
    /* Set the width to 90% */
    color: #333;
    position: absolute;
    top: 100px;
    left: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;

    .cart-table {
      display: table;
      width: 100%;
      border-collapse: collapse;
    }


    .cart-header {
      display: table-header-group;
      font-weight: bold;
      color: #000;
      /* Change to your preferred heading color */
      display: flex;
      align-items: center;
      justify-content: flex-start;
    }

    .header-cell {
      padding: 10px;
      vertical-align: top;

    }

    .cell {
      display: table-cell;
      padding: 10px;
      border-bottom: 1px solid #ccc;
      vertical-align: top;
    }

    .cart-container .day-highlight {
      color: blue;
      /* Change to your preferred color */
    }

    .available {
      color: green;
    }

    .cart-item {
      display: table-row;
    }

    .cart-item:hover {
      background-color: #f5f5f5;
      /* Change background color on hover */
    }

    .chekout-btn {
      color: #fff;
      border: none;
      padding: 8px 16px;
      border-radius: 4px;
      cursor: pointer;
      position: absolute;
      right: -8px;
    }
  }

  /* Responsive Styling */
  @media only screen and (max-width: 768px) {
    .cart-header {
      display: none;
      /* Hide the header cells on smaller screens */
    }

    .cart-container {
      width: calc(100vh - 100px);

      .cart-item {
        display: block;
        border: 1px solid #ccc;
        margin-bottom: 10px;
      }

      .cell {
        display: block;
        padding: 10px;
      }

      .cell span {
        display: block;
      }

      .cell:nth-child(odd) {
        font-weight: bold;
      }

      .chekout-btn {
        float: none;
        /* Center the button on smaller screens */
        margin: 20px auto;
        /* Center the button horizontally */
      }
    }
  }
}