/* Cancel.scss */
.cancel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;

  .icon-container {
    width: 100px;
    height: 100px;
    background-color: #ffe8e6;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }

  .cancel-icon {
    width: 60%;
    height: auto;
    fill: #f44336;
    /* Red color */
  }

  h2 {
    color: #f44336;
    /* Red color */
    font-size: 24px;
  }
}